function linearCalculation(factor, base) {
    base = base || 0;
    return value => {
        const res = base + value * factor;
        return {res, factor, base}
    }
}

export function inverseCalculation(factor) {
    return value => {
        const v0 = 1 / (1 - value) - 1
        return v0 * factor
    }
}

function printAttr(options) {
    options = options || {};
    return (attr, value, rate) => {
        if (options.flag && value == 1) {
            return attr;
        }
        if (options['pct'] == undefined || options['pct'] == true) {
            value = `${(value * 100).toFixed(0)}%`;
        }
        if (options['rate']) {
            rate = rate || 1;
            value = `${value}:${(rate * 100).toFixed(0)}%`;
        }
        const symbol = options['symbol'] || '+';
        return `${attr}${symbol}${value}`;
    };
}

